<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">  </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">  </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">   </a>
        </li>
      </ul>

      <h1 class="content__title">  <span> 23621</span></h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;  
             &nbsp; .    &nbsp;
            &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">  </span>
              <span class="dictionary__value">
                {{ order.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">   </span>
              <span class="dictionary__value">
                {{ order.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">  </span>
              <span class="dictionary__value"> {{ order.phone }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email </span>
              <span class="dictionary__value"> {{ order.email }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">   </span>
              <span class="dictionary__value"> {{order.paymentType}} </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="product in order.basket.items" :key="product.id">
              <h3>{{ product.productOffer.title }}</h3>
              <b>{{ (product.quantity * product.price) | numberFormat }} Br</b>
              <span>: {{ product.quantity }} .</span> <br>
              <span>: {{ product.productOffer.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>1200 Br</b></p>
            <p>: <b>{{ order.basket.items.length }}</b>    <b>{{ (order.totalPrice + 1200) | numberFormat}} Br</b></p>
          </div>
        </div>

      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';
import numberFormat from "@/helpers/numberFormat";

export default {
    created() {
        if(this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
            return;
        }
        this.$store.dispatch('loadOrderInfo', this.$route.params.id)
    },
    filters: {
        numberFormat
    },
    computed: {
        ...mapGetters({order: 'getOrderInfo'})
    }
    
};
</script>