<template>
  <main class="content container" v-if="productsLoading"> </main>
  <main class="content container" v-else-if="!productsData">   </main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> {{ product.title }} </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img class="img__product"
            width="500"
            height="500"
            :src="product.preview.file.url"
            :alt="product.title"
          />
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">: {{ product.id }}</span>
        <h2 class="item__title">{{ product.title }}</h2>
        <div class="item__form">
          <form
            class="form"
            action="#"
            method="POST"
            @submit.prevent="addToCart"
          >
            <b class="item__price"> {{ changePrice | numberFormat }} Br </b>

            <fieldset class="form__block">
              <legend class="form__legend">:</legend>
              <ul class="colors">
                <li class="colors__item" v-for="color in product.colors" :key="color.id">
                  <label class="colors__label">
                    <input
                      class="colors__radio sr-only"
                      type="radio"
                      name="color-item"
                      :value= color.color.id
                      v-model="colorId"
                      checked=""
                    />
                    <span
                      class="colors__value"
                      :style="{backgroundColor: color.color.code}"
                    >
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <fieldset class="form__block">
              <legend class="form__legend">{{propTitle}}:</legend>
              <ul class="sizes sizes--primery">
                <li class="sizes__item" v-for="offer in product.offers" :key="offer.id">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-1" :value='offer.id'>
                    <span class="sizes__value" 
                          :class="{ active: params === offer.id }" 
                          @click="params = offer.id">
                          {{ offer.propValues.map(item => item.value)[0] }}
                            <!-- {{ offer.propValues.map(item => {if(item.productProp.code !== 'color') return item.value})[0] }} -->
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <div class="form__counter">
                <button
                  :class="{ 'pagination__link--disabled': productAmount === 1 }"
                  type="button"
                  aria-label="  "
                  @click.prevent="productAmount--"
                >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="productAmount" />

                <button
                  type="button"
                  aria-label="  "
                  @click.prevent="productAmount++"
                >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <button class="button button--primery" type="submit" :disabled="productAddSending">
                 
              </button>
            </div>
            <div v-show="productAdded">   </div>
            <div v-show="productAddSending">   ...</div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link" href="#">  </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">  </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">  </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">    </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
             GPS, , BEIDOU Galileo  QZSS<br />
              <br />
              Bluetooth Smart, ANT+  Wi-Fi<br />
              <br />
          </p>

          <a href="#">   </a>

          <h3> ?</h3>

          <p>
            Wahoo ELEMNT BOLT GPS   ,  
              ,   
            . Wahoo ELEMNT BOLT GPS    
            ANT+,     .   
            ,     .    
                 ,  
               .  
             ,     
            .      
                .
          </p>

          <h3></h3>

          <p>
             Wahoo ELEMNT BOLT  .  
              74,6 x 47,3 x 22,1 .    
            .      .
                   
            56 .      ,  
                  :
            ,   ,   .. ( 
               ).    
               IPX7.  ,     , 
               ( 30 )    
               1 .
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";
import { API_BASE_URL } from "@/config";
import axios from "axios";
import { mapActions } from 'vuex'

export default {
  data() {
    return {
      productsData: null,
      productAmount: 1,

      productsLoading: false,
      productsLoadingFailed: false,

      productAdded: false,
      productAddSending: false,

      params: null,
      totalPrice: null,

      colorId: null,
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      return this.productsData ? this.productsData : [];
    },
    changePrice() {
      if(this.params) {
        return this.totalPrice = this.product.offers.find((item) => item.id === this.params).price
      } return this.product.price
    },
    propTitle(){
      let propTitle = this.product.offers.find(item => item.propValues).propValues.find(item => item.productProp).productProp.title
      // if(propTitle !=='') return propTitle
      return propTitle
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),

    gotoPage,
    addToCart() {
      if (this.productAmount < 1) {
        this.productAmount = 1;
        alert(
          "    , ,       "
        );
      } else {
        this.productAdded = false;
        this.productAddSending = true;
        this.addProductToCart({productOfferId: this.params, colorId: this.colorId, amount: this.productAmount})
          .then(() => {
            this.productAdded = true;
            this.productAddSending = false;
          })
          .catch(() => {
            alert('-   ,     ')
            this.productAddSending = false;
          })
        }
    },
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      axios.get(API_BASE_URL + "/api/products/" + this.$route.params.id)
        .then(response => this.productsData = response.data)
        .catch(() => this.productsLoadingFailed = true)
        .then(() => this.productsLoading = false);
    }
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProducts();
      },
      immediate: true
    }
  }
};
</script>